import React, {useEffect, useState} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Modal, ModalBody, ModalFooter, ModalHeader} from 'reactstrap';
import axios from 'axios';

function App() {
  const urlapi="http://apirest.rclj.com/Usuarios";              // Ruta del APIREST Usuarios
  const [data, setData] = useState([]);                         // Declaración función para extraer el arreglo "datos" del JSON y los almacena en la variable "data" 
  const [modalInsertar, setModalInsertar] = useState(false);    // Declaración función para manejar la variable booleana "modalInsertar" para visualizar el modal de inserción de registros
  const [usuarioElejido, setUsuarioElejido] = useState({        // Declaración función para administrar la estructura del registro de usuario
    id_usuario: '',
    nombre_usuario: '',
    correo_usuario: '',
    tipo_documento_usuario: '',
    numero_documento_usuario: ''
  });
  const controladorCambios=e=>{                                 // Manejador de campos de formulario
    const {name, value}=e.target;
    setUsuarioElejido((prevState)=>({
      ...prevState,
      [name]: value
    }))
    // console.log(usuarioElejido);
  } 
  const ShowHideModalInsertar=()=>{                             // Definición función que será invocada en el formulario para mostrar y ocultar el MOdal
    setModalInsertar(!modalInsertar);
  }
  const reqGet=async()=>{ await axios.get(urlapi)               // Definición función requerimiento "GET" que envía los datos recibidos del API (JSON) a la variable "data"
      .then(response=>{ 
        setData(response.data.datos);                                 
      }) 
  }
  const reqPost=async()=>{                                      // Definición función requerimiento "POST" que toma los datos del formulario y los envía a la API
    var frm = new FormData();
    frm.append("nombre_usuario", usuarioElejido.nombre_usuario);
    frm.append("correo_usuario", usuarioElejido.correo_usuario);
    frm.append("tipo_documento_usuario", usuarioElejido.tipo_documento_usuario);
    frm.append("numero_documento_usuario", usuarioElejido.numero_documento_usuario);
    await axios.post(urlapi, frm)
    .then(response=>{
      setData(data.concat(response.data));
      ShowHideModalInsertar();
      reqGet();
    })
  }
  useEffect(()=>{ reqGet(); }, [])                              // Ejecución del requerimeinto "GET" en la carga de la página
  return (
    <div className="App" style={{ textAlign: 'center'}}>
      <div className="container-sm">
        <br/><button className="btn btn-info btn-xs" onClick={()=>ShowHideModalInsertar()}>Insertar</button><br/><br/>
        <table className="table">
          <thead>
            <tr>
              <th>Id</th>
              <th>Nombre</th>
              <th>Correo</th>
              <th>Tipo documento</th>
              <th>Número documento</th>
              <th>Acciones</th>
            </tr>
          </thead>
          <tbody>
            { data.map(element => (                             // Por cada registro recibido se inserta una línea en la tabla HTML
                  <tr key={element.id_usuario}>
                    <td>{ element.id_usuario } </td>
                    <td>{ element.nombre_usuario }</td>
                    <td>{ element.correo_usuario }</td>
                    <td>{ element.tipo_documento_usuario }</td>
                    <td>{ element.numero_documento_usuario }</td>
                    <td>
                        <div className="btn-group-sm">
                          <button className="btn btn-primary btn-xs">Editar</button>&nbsp;
                          <button className="btn btn-danger btn-xs">Eliminar</button>
                        </div>
                    </td>
                  </tr>            
                ))
            }
          </tbody>
        </table>
        <Modal isOpen={modalInsertar} animation={"false"}>       
          <ModalHeader>Insertar un nuevo usuario</ModalHeader>
          <ModalBody>
            <div>
              <label>Nombre:</label><br/><input type="text" name="nombre_usuario" onChange={controladorCambios}/><br/>
              <label>Correo:</label><br/><input type="text" name="correo_usuario" onChange={controladorCambios}/><br/>
              <label>Tipo Documento:</label><br/><input type="text" name="tipo_documento_usuario" onChange={controladorCambios}/><br/>
              <label>Numero Documento:</label><br/><input type="text" name="numero_documento_usuario" onChange={controladorCambios}/><br/>
            </div>
          </ModalBody>
          <ModalFooter>
            <button className="btn btn-primary" onClick={()=>reqPost()}>Insertar</button>&nbsp;
            <button className="btn btn-danger" onClick={()=>ShowHideModalInsertar()}>Cancelar</button>
          </ModalFooter>
        </Modal>
      </div>
    </div>
  );
}
export default App;